// THE SMALL PRINT
// ===============
.smallPrint {
  .info {
    @include rmin(320) {
      padding-top: 45px;
      padding-bottom: 50px; }
    @include rmin(768) {
      padding-top: 90px;
      padding-bottom: 100px; }


    // CONTAINER
    .container {
      max-width: (790px + 120px); }
    // \ CONTAINER


    // TITLE
    &__title {
      padding-bottom: 50px;
      text-align: left; }
    // \ TITLE


    // PDF
    &__pdf {
      display: flex;
      align-items: center;
      min-height: 55px;
      margin-bottom: 50px;
      @include rmin(320) {
        flex-direction: column; }
      @include rmin(576) {
        flex-direction: row; }

      &-col {
        &--0 {
          width: (37px + 23px); }
        &--1 {
          width: calc(100% - (50px + 100px + (37px + 23px)));
          padding-right: 30px; }
        &--2 {
          width: 50px;
          text-align: center; }
        &--3 {
          width: 100px;
          text-align: center; }

        @include r(575) {
          width: 100%;
          padding: 0;
          text-align: center; } }

      &-title {
        font-weight: bold;
        @include rmin(320) {
          font-size: 16px; }
        @include rmin(576) {
          font-size: 20px; }
        @include rmin(728) {
          font-size: 22px; }
        @include r(575) {
          padding-top: 10px;
          padding-bottom: 10px; } }

      &-text,
      &-btn {
        @include rmin(320) {
          font-size: 14px; }
        @include rmin(576) {
          font-size: 16px; }
        @include rmin(728) {
          font-size: 18px; } }

      &-text {
        font-family: "SFUIText", "Arial", sans-serif;
        @include r(575) {
          padding-bottom: 10px; } }

      &-btn {
        color: $colorAlizarin;
        font-family: "SFUIText", "Arial", sans-serif;
        @include r(575) {
          display: block;
          padding-top: 10px;
          padding-bottom: 10px; } } }
 } }    // \ PDF
// ===============
