// HEADER :: START

.header {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 79px;
  padding-top: 29px;
  background-color: $colorWhite;
  @include transition(background-color, padding-top, height);
  @include r(767) {
    height: 50px;
    padding-top: 0; }

  .about &,
  .onlineTrading &,
  .depositsWithdrawals &,
  .equinixCybersonicExecution &,
  .contacts &,
  .tradingTools &,
  .economicCalendar & {
    background-color: transparent; }

  &.is-fixed {
    padding-top: 5px;
    height: (79px - 20px);
    @include transition(background-color, padding-top, height);
    @include r(767) {
      padding-top: 0;
      height: (79px - 29px); }

    .about &,
    .onlineTrading &,
    .depositsWithdrawals &,
    .equinixCybersonicExecution &,
    .contacts &,
    .tradingTools &,
    .tradingTools &,
    .economicCalendar & {
      background-color: $colorWhite; } }


  .container {
    max-width: 100%;
    @include r(767) {
      padding-left: 15px;
      padding-right: 15px; } }

  &__wrap {
    display: flex;
    height: (79px - 29px);
    @include r(767) {
      height: 50px; }
    @include r(1365) {
      align-items: center;
      justify-content: space-between; } }


  &__mobile {
    display: none;
    width: calc(100% - 85px);
    @include r(1365) {
      &.is-active {
        z-index: 100;
        overflow: hidden;
        overflow-y: auto;
        position: fixed;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding-top: 80px;
        padding-bottom: 15px;
        background-color: $colorWhite;
        animation-duration: 0.3s; } }
    @include rmin(1366) {
      display: flex; } }


  &__row {
    display: flex;
    align-items: center;
    width: 50%;
    &:nth-child(1) {
      justify-content: flex-start; }
    &:nth-child(2) {
      justify-content: flex-end; }
    @include r(1365) {
      width: 100%;
      flex-direction: column;
      align-items: flex-start; } } }

// HEADER :: END

