.loader {
  display: none;
  z-index: 10001;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  animation-name: slideInHeight;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.75, 0.2, 0.1, 0.5);
  animation-duration: 0.75s;
  animation-delay: 0.75s;
}

.home .loader {
  animation-duration: 0.5s;
  animation-delay: 1.75s;
}

.is-loader .loader {
  display: block;
}

.loader--fade {
  z-index: 0;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.02);
  animation-fill-mode: backwards;
  animation-duration: 0.75s;
  animation-delay: 0.2s;
}

.home .loader--fade {
  animation-duration: 0.75s;
  animation-delay: 0;
}

.loader--fade p {
  z-index: 0;
  position: relative;
  display: flex;
  letter-spacing: 0.5px;
  color: rgba(0, 0, 0, 0.5);
  font-size: 22px;
  font-weight: 500;
}

.loader--fade p span {
  display: block;
  animation-name: slideInUpText;
}

.loader--fade p span:nth-child(1) {
  margin-right: 2.5px;
  animation-delay: 0.5s;
}

.loader--fade p span:nth-child(2) {
  margin-left: 2.5px;
  animation-delay: 0.65s;
}

.loader--block {
  z-index: 1;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 75%;
  background-color: rgba(0, 0, 0, 0.01);
  animation-name: slideInTop;
  animation-timing-function: cubic-bezier(0.25, 0.45, 0.45, 0.25);
  animation-duration: 1.5s;
  animation-delay: 0.35s;
}

.home .loader--block {
  animation-duration: 2.5s;
  animation-delay: 0.75s;
}

@keyframes slideInTop {
  from {
    visibility: visible;
    transform: translate3d(0, 150%, 0);
  }
  to {
    visibility: hidden;
    transform: translate3d(0, -150%, 0);
  }
}

@keyframes slideInHeight {
  from {
    height: 100%;
    visibility: visible;
  }
  to {
    height: 0;
    visibility: hidden;
  }
}

@keyframes slideInUpText {
  from {
    opacity: 0;
    transform: translate3d(0, 150%, 0);
    visibility: visible;
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.hamburger {
  cursor: pointer;
  display: none;
  z-index: 1000;
  position: relative;
  width: 28px;
  height: 10.5px;
  transform: rotate(0deg);
  transition: .2s ease-in-out;
}

@media only screen and (max-width: 1365px) {
  .hamburger {
    display: block;
  }
}

.hamburger span {
  opacity: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  transform: rotate(0deg);
}

.hamburger span:before, .hamburger span:after {
  content: "";
  position: absolute;
  left: 0;
  height: 2px;
  width: 100%;
  background: #000;
  transition: top 0.3s ease-in-out, bottom 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.hamburger span:before {
  top: 0;
}

.hamburger span:after {
  bottom: 0;
}

.hamburger.is-open {
  z-index: 110;
}

.hamburger.is-open span:before {
  top: 4px;
  transform: rotate(-45deg);
  transition: top 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.hamburger.is-open span:after {
  bottom: 4px;
  transform: rotate(45deg);
  transition: bottom 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.header__hamburger {
  z-index: 100;
  position: relative;
  animation-delay: 0.3s;
}

.home.is-loader .header__hamburger {
  animation-delay: 2.8s;
}

.is-loader .header__hamburger {
  animation-delay: 2.05s;
}

