// ENQUIRIES
// ===============
.enquiries {
  @include rmin(320) {
    padding-bottom: 55px; }
  @include rmin(768) {
    padding-bottom: 105px; }


  // TITLE
  &__title {
    padding-bottom: 40px; }
  // \ TITLE


  // TEXT
  &__text {
    width: 100%;
    max-width: 790px;
    margin-left: auto;
    margin-right: auto;
    @include rmin(320) {
      padding-bottom: 50px; }
    @include rmin(768) {
      padding-bottom: 100px; } }
  // \ TEXT


  // ROW
  &__row {
    display: flex;
    flex-wrap: wrap; }
  // \ ROW


  // BLOCK
  &__block {
    padding-left: 15px;
    padding-right: 15px;
    text-align: center;
    @include rmin(320) {
      width: 100%;
      margin-bottom: 40px; }
    @include rmin(576) {
      width: calc(100% / 2);
      margin-bottom: 80px; }
    @include rmin(992) {
      width: calc(100% / 3);
      margin-bottom: 120px; }

    &-title {
      padding-bottom: 10px;
      font-weight: bold;
      @include rmin(320) {
        font-size: 16px; }
      @include rmin(576) {
        font-size: 20px; }
      @include rmin(728) {
        font-size: 24px; } }

    &-text {
      padding-bottom: 20px;
      color: rgba($colorBlack, 0.7);
      font-family: "SFUIText", "Arial", sans-serif;
      @include rmin(320) {
        font-size: 14px; }
      @include rmin(576) {
        font-size: 16px; }
      @include rmin(728) {
        font-size: 18px; } }

    &-email {
      color: $colorAlizarin;
      @include rmin(320) {
        font-size: 14px; }
      @include rmin(576) {
        font-size: 16px; }
      @include rmin(728) {
        font-size: 18px; }

      &-wrap {
        white-space: nowrap;
        font-family: "SFUIText", "Arial", sans-serif;

        .icon {
          margin-right: 10px; } } } } }
// \ BLOCK
// ===============
