// PORTAL
// ===============
.portal {
  padding-top: 100px;
  padding-bottom: 65px;

  // TITLE
  &__title {
    padding-bottom: 50px; }
  // \ TITLE


  // TEXT
  &__text {
    width: 100%;
    max-width: 790px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 70px; }
  // \ TITLE


  // ROW
  &__row {
    display: flex;
    width: 100%;
    max-width: 1024px;
    margin-left: auto;
    margin-right: auto;
    @include rmin(320) {
      flex-wrap: wrap; }
    @include rmin(992) {
      flex-wrap: nowrap; } }
  // \ ROW


  // COL
  &__col {
    @include rmin(320) {
      width: 100%;
      margin-bottom: 30px; }
    @include rmin(576) {
      width: calc(100% / 2); }
    @include rmin(992) {
      width: calc(100% / 3);
      margin-bottom: 0; } }
  // \ COL


  // \ COL
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    font-weight: 600;
    @include rmin(320) {
      font-size: 16px; }
    @include rmin(576) {
      font-size: 20px; }
    @include rmin(992) {
      font-size: 22px; }

    .icon {
      margin-right: 10px; } }
 }  // \ COL
// ===============
