// MAIN
// ===============
.depositsWithdrawals {
  .main {
    padding-bottom: 15px;
    background: rgb(255,255,255);
    background: -moz-linear-gradient(top, rgba(255,255,255,1) 1%, rgba(244,244,244,1) 100%);
    background: -webkit-linear-gradient(top, rgba(255,255,255,1) 1%,rgba(244,244,244,1) 100%);
    background: linear-gradient(to bottom, rgba(255,255,255,1) 1%,rgba(244,244,244,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f4f4f4',GradientType=0 );
    @include rmin(320) {
      padding-top: 101px; }
    @include rmin(768) {
      padding-top: 201px; }


    // CONTAINER
    .container {
      &--small {
        max-width: (790px + 120px); } }
    // end CONTAINER


    // TITLE
    &__title {
      line-height: normal;
      @include rmin(320) {
        padding-bottom: 30px;
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        padding-bottom: 60px;
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end TITLE


    // SUBTITLE
    &__subtitle {
      text-align: left;
      @include rmin(320) {
        padding-bottom: 30px; }
      @include rmin(768) {
        padding-bottom: 60px; } }
    // end SUBTITLE


    // TEXT
    &__text {
      text-align: left;

      &--p {
        padding-top: 30px;
        @include rmin(320) {
          padding-bottom: 30px; }
        @include rmin(768) {
          padding-bottom: 60px; } } }
    // end TEXT


    // ROW
    &__row {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      @include rmin(1200); }
    // end ROW


    // BLOCK
    &__block {
      cursor: pointer;
      overflow: hidden;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: 347px;
      background-color: $colorWhite;
      font-family: "SFUIText", "Arial", sans-serif;
      @include rmin(320) {
        max-width: 100%;
        padding: 15px 15px 80px;
        margin-bottom: 30px; }
      @include rmin(576) {
        max-width: 265px; }
      @include rmin(768) {
        max-width: 315px;
        padding: 30px;
        margin-bottom: 70px; }
      @include rmin(992) {
        max-width: 395px;
        padding: 40px 50px; }
      @include rmin(1200) {
        max-width: 355px;
        padding: 30px 40px; }
      @include rmin(1366) {
        max-width: 395px;
        padding: 40px 50px; }

      &:hover {
        .main__block-btn {
          transform: translateY(0);
          @include transition(transform); } }

      &-img {
        margin-right: 15px;

        &-wrap {
          display: flex;
          align-items: center;
          padding-bottom: 15px; } }

      &-name {
        padding-bottom: 45px;
        text-transform: uppercase;
        color: $colorGrey;
        font-weight: 600;
        font-size: 13px; }

      &-currency {
        padding-bottom: 10px;
        text-transform: uppercase;
        color: $colorGrey;
        font-weight: 600;
        font-size: 13px; }

      &-list {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 56px; }

      &-item {
        width: 55px;
        padding-bottom: 5px;
        font-size: 18px;
        font-weight: bold; }

      &-process {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        color: $colorGrey;
        font-size: 14px;

        .icon {
          margin-right: 10px; } }

      &-btn {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 60px;
        background-color: $colorBlack;
        color: $colorWhite;

        @include transition(transform);
        @include rmin(320) {
          transform: translateY(0); }
        @include rmin(768) {
          transform: translateY(60px); } } }
 } }    // end BLOCK
// ===============
