// MAIN
// ===============
.learn {
  .main {
    position: relative;
    @include rmin(320) {
      padding-bottom: 30px; }
    @include rmin(768) {
      padding-bottom: 50px; }


    // BG
    &__bg {
      z-index: 0;
      position: absolute;
      height: 100%;
      max-height: calc(100% - (40px + 79px));
      background-color: $colorWhiteSmoke;
      background-image: url("../img/img-bg_learn@2x.png");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      @include rmin(320) {
        top: (20px + 50px);
        left: 15px;
        right: 15px;
        width: calc(100% - 30px); }
      @include rmin(768) {
        top: (40px + 79px);
        left: 60px;
        right: 60px;
        width: calc(100% - 120px); } }
    // end BG


    // CONTAINER
    .container {
      z-index: 1;
      position: relative;
      height: 100%;
      &--small {
        max-width: 1440px;
        @include rmin(320);
        @include rmin(768) {
          padding-left: 30px;
          padding-right: 30px; }
        @include rmin(992) {
          padding-left: 60px;
          padding-right: 60px; } } }

    // end CONTAINER


    // MAIN TITLE
    .main__title {
      padding-bottom: 50px;
      line-height: normal;
      @include rmin(320) {
        padding-top: 120px;
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        padding-top: 200px;
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end MAIN TITLE


    // SUBTITLE
    &__subtitle {
      padding-bottom: 50px;
      color: $colorAlizarin;
      text-align: left; }
    // \ SUBTITLE


    // BTN
    &__btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 266px;
      height: 60px;
      margin-bottom: 93px;
      background-color: $colorAlizarin;
      color: $colorWhite;
      font-weight: bold;
      @include transition(background-color);
      @include rmin(320) {
        font-size: 18px; }
      @include rmin(576) {
        font-size: 22px; }

      &:hover {
        background-color: $colorBlack;
        @include transition(background-color); } }
 } }    // \ BTN
// ===============
