// SIGN :: START
.sign {
  @include rmin(1366) {
    display: flex;
    align-items: center; }
  @include r(1365) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding-left: 15px; }

  &__link {
    display: block;
    position: relative;
    margin-left: 30px;
    @include link();
    @include transition(color);
    &:first-child {
      margin-left: 0;
      animation-delay: 0.5s;
      @include r(1365) {
        height: 70px;
        line-height: 70px;
        animation-name: fadeInUp !important;
        animation-delay: 0.9s !important;
        visibility: visible !important; }

      .home.is-loader & {
        animation-delay: 3s; }

      .is-loader & {
        animation-delay: 2.25s; }

      .sign__link-btn {
        @include r(767) {
          font-size: 18px;
          font-weight: 600; } } }

    &:nth-child(2) {
      animation-delay: 0.4s;
      @include r(1365) {
        height: 70px;
        margin-left: 0;
        line-height: 70px;
        animation-name: fadeInUp !important;
        animation-delay: 1s !important;
        visibility: visible !important; }
      @include r(767) {
        display: block;
        width: 100%;
        height: 50px;
        line-height: 50px;
        max-width: 290px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 10px;
        background-color: rgba($colorBlack, 0.1);
        text-align: center; }

      .home.is-loader & {
        animation-delay: 2.9s; }

      .is-loader & {
        animation-delay: 2.15s; } }

    &-btn {
      display: block;
      @include rmin(1366) {
        &:hover {
          color: $colorAlizarin;
          @include transition(color); }

        .onlineTrading &,
        .about & {
          color: $colorWhite;
          &:hover {
            color: $colorAlizarin; } }

        .onlineTrading .is-fixed &,
        .about .is-fixed & {
          color: $colorBlack;
          &:hover {
            color: $colorAlizarin; } } } } }


  &__btn {
    display: block;
    margin-left: 20px;
    padding: 11px 20px 10px;
    border: 1px solid rgba($colorBlack, 0.1);
    border-radius: 3px;
    animation-delay: 0.3s;
    @include link();
    @include transition(border-color, box-shadow, background-color, color);
    @include r(1365) {
      margin-left: 0;
      animation-name: fadeInUp !important;
      animation-delay: 1.1s !important;
      visibility: visible !important; }
    @include r(767) {
      display: block;
      width: 100%;
      height: 50px;
      line-height: 50px;
      max-width: 290px;
      margin-left: auto;
      margin-right: auto;
      padding: 0;
      border-radius: 0;
      background-color: $colorAlizarin;
      color: $colorWhite;
      text-align: center; }

    &:hover {
      border-color: $colorAlizarin;
      box-shadow: 0 0 5px 0 rgba($colorBlack, 0.1);
      background-color: $colorAlizarin;
      color: $colorWhite;
      @include transition(border-color, box-shadow, background-color, color); }

    .home.is-loader & {
      animation-delay: 2.8s; }

    .is-loader & {
      animation-delay: 2.05s; }

    .onlineTrading &,
    .about & {
      color: $colorWhite;
      border-color: $colorWhite;
      &:hover {
        border-color: $colorAlizarin; } }

    .onlineTrading .is-fixed &,
    .about .is-fixed & {
      color: $colorBlack;
      border-color: $colorBlack;
      &:hover {
        color: $colorWhite;
        border-color: $colorAlizarin; } } } }

// SIGN :: END
