// INFO
// ===============
.learn {
  .info {
    @include rmin(320) {
      padding-top: 40px;
      padding-bottom: 84px; }
    @include rmin(768) {
      padding-top: 80px;
      padding-bottom: 184px; }

    &#yourcommitment {
      background-color: $colorWhiteSmoke; }

    &--learn {
      padding-bottom: 0; }

    // CONTAINER
    .container {
      &--small {
        max-width: (790px + 120px); } }
    // end CONTAINER


    // TITLE
    &__title {
      text-align: left;
      @include rmin(320) {
        padding-top: 30px;
        padding-bottom: 30px; }
      @include rmin(768) {
        padding-top: 60px;
        padding-bottom: 30px; }

      &--center {
        text-align: center; } }
    // end TITLE


    // SUBTITLE
    &__subtitle {
      padding-top: 30px;
      padding-bottom: 30px;
      text-align: left;
      &--center {
        text-align: center; }
      &--color {
        color: $colorAlizarin; } }
    // \ SUBTITLE


    // TEXT
    &__text {
      text-align: left;

      &--p {
        padding-top: 30px; }
      &--center {
        text-align: center; } }
    // end TEXT


    // IMG
    &__img {
      margin-top: 30px;
      display: block;
      max-width: 100%;
      height: auto; }
    // \ IMG


    // BTN
    &__btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 266px;
      height: 60px;
      margin-left: auto;
      margin-right: auto;
      background-color: $colorAlizarin;
      color: $colorWhite;
      font-weight: bold;
      @include transition(background-color);
      @include rmin(320) {
        font-size: 18px; }
      @include rmin(576) {
        font-size: 22px; }

      &:hover {
        background-color: $colorBlack;
        @include transition(background-color); } }
    // \ BTN


    // ROW
    &__row {
      display: flex;
      padding-top: 50px;
      padding-bottom: 50px;
      @include rmin(320) {
        flex-direction: column; }
      @include rmin(768) {
        flex-direction: row;
        justify-content: space-between; } }
    // \ ROW


    // BLOCK
    &__block {
      cursor: pointer;
      position: relative;
      display: flex;
      margin-bottom: 50px;
      @include transition(opacity);
      @include rmin(320) {
        flex-direction: column; }
      @include rmin(576) {
        flex-direction: row; }
      @include rmin(768) {
        opacity: 0.4;
        flex-direction: column;
        width: calc(100% / 3); }

      &.is-active {
        opacity: 1;
        @include transition(opacity);
        .info__block-text {
          opacity: 1;
          @include transition(opacity); } }

      &:last-child {
        margin-bottom: 0; }

      &-left {
        padding-bottom: 30px;
        text-align: center;
        padding-left: 10px;
        padding-right: 10px; }

      &-right {
        width: 100%; }

      &-img {
        max-width: 100%; }

      &-text {
        font-family: "SFUIText", "Rajdhani", "Arial", sans-serif;
        padding-bottom: 30px;
        @include transition(opacity);
        @include rmin(320) {
          font-size: 16px; }
        @include rmin(576) {
          padding-left: 15px; }
        @include rmin(768) {
          opacity: 0;
          white-space: normal;
          padding-left: 0; }
        @include rmin(1200) {
          white-space: nowrap;
          font-size: 18px; }

        span {
          font-weight: bold; } } } } }
// \ BLOCK
// ===============
