// MAIN
// ===============
.tradingTools {
  .main {
    position: relative;
    background-color: #F0F4F7;
    @include rmin(320) {
      padding-top: 101px; }
    @include rmin(768) {
      padding-top: 201px; }


    // BG
    &__bg {
      z-index: 0;
      position: absolute;
      display: flex;
      align-items: center;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @include r(767) {
        display: none; }

      &-left,
      &-right {
        height: 100%; }

      &-left {
        width: 55%; }

      &-right {
        width: 45%; }

      &-right {
        background-image: url("../img/img-main-bg_tradingTools@2x.png");
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat; } }
    // \ BG


    // CONTAINER
    .container {
      z-index: 1;
      position: relative;
      height: 100%; }
    // \ CONTAINER


    // ROW
    &__row {
      display: flex;
      @include r(767) {
        flex-direction: column; } }
    // \ ROW


    // COL
    &__col {
      width: 50%;
      @include r(767) {
        width: 100%; } }
    // \ COL


    // TITLE
    &__title {
      padding-bottom: 30px;
      line-height: normal;
      @include rmin(320) {
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end TITLE


    // SUBTITLE
    &__subtitle {
      padding-bottom: 30px;
      text-align: left; }
    // end SUBTITLE


    // TEXT
    &__text {
      text-align: left;

      &--p {
        padding-top: 30px;
        padding-bottom: 60px; } }
    // end TEXT

    // BTN
    &__btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 396px;
      height: 60px;
      background-color: $colorAlizarin;
      color: $colorWhite;
      font-weight: bold;
      text-transform: uppercase;
      @include transition(background-color);
      @include rmin(320) {
        margin-bottom: 53px;
        font-size: 18px; }
      @include rmin(576) {
        margin-bottom: 133px;
        font-size: 22px; }
      @include rmin(768) {
        margin-bottom: 233px; }

      &:hover {
        background-color: $colorBlack;
        @include transition(background-color); } }
 } }    // \ BTN
// ===============
