// SECTION-5
// ===============
.section-5 {
  position: relative;
  overflow: hidden;
  .container {
    padding: 20px;
    @include rmin(768) {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }
    @include r($mobile) {
      padding: 15px; } }


  .section__hover {
    &-0,
    &-1,
    &-2 {
      cursor: pointer;
      overflow: hidden;
      position: relative;

      &:before {
        content: "";
        z-index: 0;
        position: absolute;
        bottom: 0;
        @include transition(transform);
        transition-duration: 0.5s; }

      &:hover {
        &:before {
          transform: scale(1.05);
          @include transition(transform);
          transition-duration: 0.5s; } }

      > * {
        z-index: 1;
        position: relative; } }

    &-0 {
      &:before {
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        margin-left: auto;
        margin-right: auto;
        background-image: url("../img/img_wallStreet_bg-desktop@2x.png");
        background-repeat: no-repeat;
        background-position: center bottom;
        background-size: contain;
        @include r(767) {
          background-image: url("../img/img_wallStreet_bg@2x.png");
          background-position-y: 90px; }
        @include r(575) {
          background-position-y: 155px;
          background-position-x: 20px; } } }

    &-1 {
      &:before {
        @include rmin(1200) {
          width: 273px;
          height: 298px;
          left: 12px;
          bottom: 0;
          background-image: url("../img/img-secretWeapon_bg@2x.png");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain; } } }

    &-2 {
      &:before {
        @include rmin(992) {
          width: 271px;
          height: 100%;
          right: 0;
          background-image: url("../img/img-fast_bg@2x.png");
          background-repeat: no-repeat;
          background-position: left;
          background-size: cover; } } } }

  .section__row {
    height: 100%;
    font-size: 0; }


  .section__col {
    display: inline-block;
    vertical-align: top;
    width: calc(50% - 10px);
    height: 100%;
    @include r(767) {
      display: block;
      width: 100%;
      min-height: 290px; }

    &:nth-child(1) {
      margin-right: 10px;
      background-color: $colorWhiteSmoke;

      @include r(767) {
        padding: 30px;
        margin-right: 0;
        margin-bottom: 20px; }

      .section__title {
        font-weight: 700;
        text-align: center;
        @include r(767) {
          padding-top: 0;
          padding-bottom: 20px;
          font-size: 26px; }
        @include rmin(768) {
          padding-top: 62px;
          padding-bottom: 33px;
          font-size: 42px; }
        @include rmin(1440) {
          font-size: 60px; } }

      .section__text {
        width: 100%;
        max-width: 360px;
        margin-left: auto;
        margin-right: auto;
        padding-bottom: 30px;
        text-align: center;
        line-height: 1.5;
        font-size: 16px;
        font-family: "SFUIText", "Rajdhani", "Arial", sans-serif;
        @include r(767) {
          max-width: 100%;
          text-align: center;
          font-size: 14px; } }

      .section__link {
        margin-left: auto;
        margin-right: auto; } }

    &:nth-child(2) {
      margin-left: 10px;
      @include r(767) {
        margin-left: 0; } }

    .section__link {
      display: block;
      position: relative;
      width: 100%;
      max-width: 170px;
      letter-spacing: 1px;
      font-family: "SFUIText", "Rajdhani", "Arial", sans-serif;
      font-weight: 600;
      font-size: 16px;
      @include transition(color);
      @include r(767) {
        font-size: 14px;
        margin-left: auto;
        margin-right: auto; }
      &:hover {
        color: $colorAlizarin;
        @include transition(color);

        span {
          &:before {
            width: 100%;
            background-color: $colorAlizarin;
            @include transition(width, background-color); } } }

      span {
        position: absolute;
        top: 50%;
        right: 0;
        width: 60px;
        height: 2px;
        transform: translateY(-50%);
        background-color: rgba($colorBlack, 0.1);

        &:before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 50%;
          height: 100%;
          background-color: $colorBlack;
          @include transition(width, background-color); } } } }


  .section__cover {
    height: calc(50% - 10px);
    background-color: $colorWhiteSmoke;
    @include r(767) {
      height: 100%;
      min-height: 290px; }

    &:nth-child(1) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 20px;
      padding-right: 30px;
      @include rmin(992) {
        padding-left: 30px;
        padding-right: 30px; }
      @include rmin(1200) {
        padding-left: 290px; }
      @include rmin(1366);
      @include rmin(1440);
      @include r(991) {
        padding: 30px;
        background-image: none; }

      img {
        display: none;
        @include r(767) {
          display: block;
          margin-left: auto;
          margin-right: auto; } }


      .section__title {
        padding-top: 27px;
        padding-bottom: 20px;
        font-size: 40px;
        font-weight: 700;
        @include r(767) {
          text-align: center;
          font-size: 26px; } }

      .section__text {
        padding-bottom: 20px;
        line-height: 1.5;
        font-size: 16px;
        font-family: "SFUIText", "Rajdhani", "Arial", sans-serif;
        @include r(767) {
          text-align: center;
          font-size: 14px; } }

      .section__link {
        margin-bottom: 20px; } }

    &:nth-child(2) {
      padding-left: 50px;
      padding-top: 60px;
      @include r(991) {
        padding: 30px;
        background-image: none; }
      @include r(767) {
        display: flex;
        flex-direction: column;
        justify-content: center; }

      img {
        margin-bottom: 30px;
        @include r(767) {
          display: block;
          margin-left: auto;
          margin-right: auto; } }

      .section__title {
        font-size: 40px;
        font-weight: 700;
        @include r(767) {
          text-align: center;
          font-size: 26px; } }

      .section__text {
        width: 100%;
        max-width: 360px;
        padding-top: 20px;
        padding-bottom: 32px;
        line-height: 1.4;
        font-size: 16px;
        font-family: "SFUIText", "Rajdhani", "Arial", sans-serif;
        @include r(767) {
          max-width: 100%;
          text-align: center;
          font-size: 14px; } } } } }
// ===============
