// INFO
// ===============
.gettingDownBusiness {

  .info {
    @include rmin(320) {
      padding-top: 45px;
      padding-bottom: 50px; }
    @include rmin(768) {
      padding-top: 90px;
      padding-bottom: 100px; }


    &__title,
    &__text {
      width: 100%;
      max-width: 790px;
      margin-left: auto;
      margin-right: auto; }

    &__title {
      padding-bottom: 30px;
      text-align: center;
      font-weight: bold;
      @include rmin(320) {
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        font-size: 40px; }
      @include rmin(992) {
        font-size: 44px; } }

    &__text {
      @include rmin(320) {
        padding-bottom: 35px; }
      @include rmin(768) {
        padding-bottom: 70px; } }

    &__row {
      display: flex;
      @include r(767) {
        flex-direction: column; } }

    &__col {
      display: flex;
      flex-direction: column;
      width: 50%;
      @include r(767) {
        width: 100%; }
      &:nth-child(1) {
        align-items: flex-start; }
      &:nth-child(2) {
        align-items: flex-end; } }

    &__block {
      width: 100%;
      margin-bottom: 60px;
      border: 1px solid rgba($colorBlack, 0.05);
      box-shadow: 0 2px 14px 0 rgba($colorBlack, 0.1);
      @include r(767) {
        margin-bottom: 30px; }
      @include rmin(768) {
        max-width: 300px; }
      @include rmin(992) {
        max-width: 400px; }
      @include rmin(1200) {
        max-width: 500px; }
      @include rmin(1366) {
        max-width: 580px; }
      @include rmin(1440) {
        max-width: 620px; }

      &:last-child {
        margin-bottom: 0;
        @include r(767) {
          margin-bottom: 30px; } }

      &-header {
        display: flex;
        align-items: center;
        height: 160px;
        background-color: $colorWhiteSmoke;
        @include r(767) {
          height: 100px;
          padding: 20px; }
        @include rmin(768) {
          padding: 25px; }
        @include rmin(992) {
          padding: 35px; }
        @include rmin(1200) {
          padding: 45px; } }

      &-body {
        @include r(767) {
          padding: 30px; }
        @include rmin(768) {
          padding: 30px 40px; }
        @include rmin(992) {
          padding: 40px 50px; }
        @include rmin(1200) {
          padding: 60px 70px; } }

      &-circle {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 70px;
        height: 70px;
        margin-right: 40px;
        border-radius: 100%;
        background-color: $colorAlizarin; }

      &-title {
        color: $colorAlizarin;
        text-align: left;
        @include r(575) {
          font-size: 20px; } }

      &-text {
        line-height: 1.6;
        font-family: "SFUIText", "Arial", sans-serif;
        @include rmin(320) {
          font-size: 14px; }
        @include rmin(576) {
          font-size: 16px; }
        @include rmin(728) {
          font-size: 18px; } } } } }

// ===============
