// MAIN
// ===============
.contacts {
  .main {
    background: $colorWhiteSmokeLight;
    background-image: url("../img/img-main-bg_contacts@2x.png");
    background-position: top right;
    background-size: contain;
    background-repeat: no-repeat;
    @include rmin(320) {
      padding-top: 101px; }
    @include rmin(768) {
      padding-top: 201px; }


    // TITLE
    .container {
      position: relative;
      height: 100%;
      @include rmin(320) {
        padding-bottom: 101px; }
      @include rmin(768) {
        padding-bottom: 161px; } }
    // TITLE


    // TITLE
    &__title {
      line-height: normal;
      @include rmin(320) {
        padding-bottom: 40px;
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end TITLE


    // SUBTITLE
    &__subtitle {
      width: 100%;
      max-width: 932px;
      text-align: left; }
    // end SUBTITLE


    // CIRCLE
    &__circle {
      cursor: pointer;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      background-color: $colorAlizarin;
      @include transition(box-shadow);
      @include rmin(320) {
        left: 15px;
        bottom: -45px;
        width: 90px;
        height: 90px; }
      @include rmin(768) {
        left: 60px;
        bottom: -64px;
        width: 128px;
        height: 128px; }

      &:hover {
        box-shadow: 0 0 25px 0 rgba($colorBlack, 0.25);
        @include transition(box-shadow);

        .icon {
          animation-name: tada;
          animation-iteration-count: infinite; } } }
 } }    // \ CIRCLE
// ===============
