// MAIN
// ===============
.equinixCybersonicExecution {
  .main {
    background-color: $colorWhiteSmoke;
    @include rmin(320) {
      padding-top: 101px;
      padding-bottom: 64px; }
    @include rmin(768) {
      padding-top: 201px;
      padding-bottom: 124px; }


    // ROW
    &__row {
      display: flex;
      @include rmin(320) {
        flex-direction: column;
        padding-bottom: 30px; }
      @include rmin(768) {
        flex-direction: row;
        padding-bottom: 70px; } }
    // \ ROW


    // COL
    &__col {
      @include rmin(320) {
        width: 100%; }
      @include rmin(576) {
        width: 50%; }
      &:nth-child(2) {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end; } }
    // \ COL


    // TITLE
    .main__title {
      line-height: normal;
      text-align: left;
      @include rmin(320) {
        padding-bottom: 30px;
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        padding-bottom: 0;
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // \ TITLE


    // TEXT
    &__text {
      width: 100%;
      max-width: (790px + 120px);
      text-align: left;
      &--p {
        padding-top: 30px;
        @include rmin(320) {
          padding-bottom: 50px; }
        @include rmin(768) {
          padding-bottom: 80px; }
        @include rmin(1299) {
          padding-bottom: 110px; } } }
    // \ TEXT


    // BOX
    &__box {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      background-color: $colorAlizarin;
      @include rmin(320) {
        padding: 30px; }
      @include rmin(768) {
        padding: 32px 43px; }
      @include rmin(992) {
        max-width: 361px;
        height: 234px;
        padding: 42px 53px; }
      @include rmin(1400) {
        max-width: 521px;
        height: 264px;
        padding: 62px 73px; }

      &-text {
        color: $colorWhite;
        font-weight: bold;
        text-transform: uppercase;
        @include rmin(320) {
          font-size: 22px; }
        @include rmin(768) {
          font-size: 32px; }
        @include rmin(992) {
          font-size: 42px; }
        @include rmin(1440) {
          font-size: 60px; } } }
    // \ BOX


    // WRAP
    &__wrap {
      display: flex;
      @include rmin(320) {
        flex-direction: column; }
      @include rmin(768) {
        flex-direction: row; } }
    // \ WRAP


    // BLOCK
    &__block {
      display: flex;
      @include rmin(320) {
        padding-bottom: 30px; }
      @include rmin(768) {
        width: 50%; }
      @include rmin(992) {
        padding-bottom: 61px; }
      @include rmin(1200) {
        width: auto; }

      &-0 {
        @include rmin(1200) {
          padding-right: 263px; } }
      &-2 {
        @include rmin(1200) {
          padding-left: 263px;
          padding-right: 211px; } }

      &-left {
        width: 104px; }

      &-right {
        width: calc(100% - 104px); }

      &-icon {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 70px;
        height: 70px;
        border-radius: 100%;
        background-color: $colorAlizarin;
        @include transition(box-shadow);

        &:hover {
          box-shadow: 0 0 15px 0 rgba($colorBlack, 0.15);
          @include transition(box-shadow); } }

      &-title,
      &-text {
        font-family: "SFUIText", "Arial", sans-serif; }

      &-title {
        padding-bottom: 10px;
        letter-spacing: 0.3px;
        font-weight: 600;
        @include rmin(320) {
          font-size: 16px; }
        @include rmin(576) {
          font-size: 22px; }
        @include rmin(992) {
          font-size: 32px; } }

      &-text {
        line-height: 1.6;
        @include rmin(320) {
          font-size: 14px; }
        @include rmin(768) {
          font-size: 16px; }
        @include rmin(992) {
          font-size: 18px; } } }
 } }    // \ BLOCK
// ===============
