* {
  outline: none; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  color: inherit;
  text-decoration: none;
  background-color: transparent;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  -webkit-tap-highlight-color: transparent; }

body {
  &.is-visible {
    #barba-wrapper {
      animation-name: fadeIn;
      animation-delay: 0.5s; } }


  &.is-hidden {
    #barba-wrapper {
      opacity: 0; } } }

body,
html {
  position: relative;
  background: white;
  color: $fontColor;
  font-family: 'Rajdhani', Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.2;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &.is-hideScroll {
    overflow: hidden; } }


.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column; } }

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 60px;
  padding-right: 60px;
  @include rmin(320) {
    max-width: 100%; }

  .about &,
  .onlineTrading &,
  .depositsWithdrawals &,
  .equinixCybersonicExecution &,
  .gettingDownBusiness &,
  .partnership &,
  .infoCentre &,
  .contacts &,
  .getTrading &,
  .smallPrint &,
  .accountOpening &,
  .collaborate &,
  .pipsqueaks &,
  .tradingTools &,
  .economicCalendar &,
  .footer & {
    max-width: 1440px; } }

@include r($mobile) {
  .container {
    padding-left: 15px;
    padding-right: 15px; } }

