.hamburger {
  cursor: pointer;
  display: none;
  z-index: 1000;
  position: relative;
  width: 28px;
  height: 10.5px;
  transform: rotate(0deg);
  transition: .2s ease-in-out;
  @include r(1365) {
    display: block; }

  span {
    opacity: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    transform: rotate(0deg);

    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 0;
      height: 2px;
      width: 100%;
      background: $colorBlack;
      @include transition(top, bottom, transform); }

    &:before {
      top: 0; }
    &:after {
      bottom: 0; } }

  &.is-open {
    z-index: 110;

    span {
      &:before {
        top: 4px;
        transform: rotate(-45deg);
        @include transition(top, transform); }

      &:after {
        bottom: 4px;
        transform: rotate(45deg);
        @include transition(bottom, transform); } } } }

.header__hamburger {
  z-index: 100;
  position: relative;
  animation-delay: 0.3s;

  .home.is-loader & {
    animation-delay: 2.8s; }
  .is-loader & {
    animation-delay: 2.05s; } }
