// SUCCESS
// ===============
.success {
  @include rmin(320) {
    padding-top: 20px;
    padding-bottom: 30px; }
  @include rmin(576) {
    padding-top: 60px;
    padding-bottom: 50px; }
  @include rmin(768) {
    padding-top: 116px;
    padding-bottom: 95px; }


  // CONTAINER
  .container {
    z-index: 1;
    position: relative; }
  // end CONTAINER


  // TITLE
  &__title {
    text-align: left; }
  // end TITLE


  // ROW
  &__row {
    @include r(1199) {
      display: flex;
      flex-wrap: wrap; } }
  // \ ROW


  // BLOCK
  &__block {
    display: flex;
    width: 100%;
    max-width: 420px;
    @include rmin(320) {
      padding-top: 15px;
      padding-bottom: 15px; }
    @include rmin(768) {
      width: 50%;
      padding-top: 30px;
      padding-bottom: 30px;
      margin-top: 30px;
      margin-bottom: 30px; }
    @include rmin(992) {
      padding-top: 0;
      padding-bottom: 0; }
    @include r(991) {
      max-width: 100% !important; }

    &-0 {
      @include rmin(1200) {
        padding-top: 97px;
        padding-bottom: 234px;
        margin-top: 0;
        margin-bottom: 0; } }

    &-4 {
      @include rmin(1200) {
        padding-bottom: 65px; } }

    &-1,
    &-2,
    &-3,
    &-5 {
      @include rmin(1200) {
        position: absolute; } }

    &-1 {
      top: 66px;
      left: 638px; }

    &-2 {
      top: 235px;
      right: 0; }

    &-3 {
      top: 426px;
      left: 578px; }

    &-5 {
      bottom: 0;
      right: 120px; }

    &-left {
      padding-left: 20px;
      @include rmin(320) {
        width: 65px; }
      @include rmin(576) {
        width: 100px; } }

    &-right {
      @include rmin(320) {
        width: calc(100% - 65px); }
      @include rmin(576) {
        width: calc(100% - 100px); } }

    &-num {
      font-weight: bold;
      color: $colorAlizarin;
      @include rmin(320) {
        font-size: 32px; }
      @include rmin(768) {
        font-size: 40px; }
      @include rmin(992) {
        font-size: 60px; }
      @include rmin(1440) {
        font-size: 80px; } }

    &-title {
      padding-top: 10px;
      padding-bottom: 10px;
      font-size: 22px;
      font-weight: bold; }

    &-text {
      line-height: 1.5;
      color: rgba($colorBlack, 0.7);
      font-family: "SFUIText", "Arial", sans-serif; } }
 }  // end BLOCK
// ===============
