// MAIN
// ===============
.partnership {
  .main {
    position: relative;
    @include rmin(320) {
      padding-bottom: 30px; }
    @include rmin(768) {
      padding-bottom: 50px; }


    // BG
    &__bg {
      z-index: 0;
      position: absolute;
      height: 100%;
      background-color: $colorWhiteSmoke;
      background-image: url("../img/img-main-bg_partnership@2x.jpg");
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      @include rmin(320) {
        top: (20px + 50px);
        left: 15px;
        right: 15px;
        width: calc(100% - 30px);
        max-height: calc(100% - (20px + 50px)); }
      @include rmin(768) {
        top: (40px + 79px);
        left: 60px;
        right: 60px;
        width: calc(100% - 120px);
        max-height: calc(100% - (40px + 79px)); } }
    // end BG


    // CONTAINER
    .container {
      z-index: 1;
      position: relative;
      height: 100%;
      max-width: 100%;
      &--small {
        max-width: 1440px;
        @include rmin(768) {
          padding-left: 30px;
          padding-right: 30px; }
        @include rmin(992) {
          padding-left: 60px;
          padding-right: 60px; } } }

    // end CONTAINER


    // MAIN TITLE
    .main__title {
      line-height: normal;
      @include rmin(320) {
        padding-top: 120px;
        padding-bottom: 30px;
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        padding-top: 200px;
        padding-bottom: 70px;
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end MAIN TITLE


    // SUBTITLE
    &__subtitle {
      color: $colorAlizarin;
      text-align: left;
      text-transform: uppercase;
      @include rmin(320) {
        padding-bottom: 35px; }
      @include rmin(768) {
        padding-bottom: 70px; } }
    // \ SUBTITLE


    // TEXT
    &__text {
      width: 100%;
      max-width: 790px;
      text-align: left;
      @include rmin(320) {
        padding-bottom: 35px; }
      @include rmin(576) {
        padding-bottom: 75px; }
      @include rmin(768) {
        padding-bottom: 125px; }
      @include rmin(1200) {
        padding-bottom: 195px; } }
 } }    // \ TEXT
// ===============
