// SERVERS
// ===============
.servers {
  @include rmin(320) {
    padding-top: 15px;
    padding-bottom: 30px; }
  @include rmin(768) {
    //padding-top: 75px
    padding-bottom: 50px; }


  // TITLE
  &__title {
    padding-bottom: 23px; }
  // end TITLE


  // SUBTITLE
  &__subtitle {
    padding-bottom: 45px; }
  // end SUBTITLE


  // TEXT
  &__text {
    width: 100%;
    max-width: 530px;
    margin-left: auto;
    margin-right: auto;
    @include rmin(320) {
      padding-bottom: 40px; }
    @include rmin(768) {
      padding-bottom: 68px; } }
  // end TEXT


  // ROW
  &__row {
    display: flex;
    justify-content: center;
    @include rmin(320) {
      flex-wrap: wrap; }
    @include rmin(1200) {
      flex-wrap: nowrap; } }
  // end ROW


  // BLOCK
  &__block {
    cursor: pointer;
    width: 100%;
    min-height: 265px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 9px;
    @include transition(box-shadow);
    @include rmin(320) {
      max-width: 310px; }
    @include rmin(768) {
      flex-wrap: wrap;
      max-width: 295px; }
    @include rmin(992) {
      max-width: 310px; }
    @include rmin(1200) {
      flex-wrap: nowrap; }

    &:nth-child(1),
    &:nth-child(4) {
      background-color: $colorBlackRussian; }

    &:nth-child(2),
    &:nth-child(3) {
      background-color: $colorAlizarin; }

    &-num {
      padding-top: 67px;
      padding-bottom: 19px;
      color: $colorWhite;
      line-height: 1;
      font-weight: 600;
      @include rmin(320) {
        font-size: 40px; }
      @include rmin(992) {
        font-size: 42px; }
      @include rmin(1440) {
        font-size: 60px; } }

    &-title {
      padding-bottom: 10px;
      color: $colorWhite;
      font-family: "SFUIText", "Arial", sans-serif;
      font-weight: 600;
      @include rmin(320) {
        font-size: 14px; }
      @include rmin(576) {
        font-size: 16px; }
      @include rmin(728) {
        font-size: 18px; } }

    &-text {
      padding-left: 15px;
      padding-right: 15px;
      text-align: center;
      font-family: "SFUIText", "Arial", sans-serif;
      color: rgba($colorWhite, 0.5); } }
 }  // end BLOCK
// ===============
