// WHAT
// ===============
.what {
  position: relative;
  @include rmin(320) {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include rmin(768) {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 85px;
    margin-bottom: 15px; }


  // ROW
  &__bg {
    position: absolute;
    left: 0;
    width: 100%;
    background-color: $colorWhiteSmoke;
    @include rmin(320) {
      top: 0;
      height: 100%; }
    @include rmin(768) {
      top: 60px;
      height: calc(100% - 45px); } }
  // end ROW


  // ROW
  &__row {
    display: flex;
    @include rmin(320) {
      flex-direction: column; }
    @include rmin(768) {
      flex-direction: row; } }
  // ROW


  // COL
  &__col {
    position: relative;
    @include rmin(320) {
      width: 100%; }
    @include rmin(768) {
      width: 50%; }
    &:nth-child(1) {
      @include rmin(768) {
        padding-top: 130px;
        padding-right: 80px;
        padding-bottom: 30px; }
      @include rmin(992) {
        padding-top: 180px;
        padding-right: 130px;
        padding-bottom: 48px; } }
    &:nth-child(2) {
      position: relative;
      @include rmin(320) {
        display: none; }
      @include rmin(768) {
        display: block; }
      &:before {
        content: "";
        z-index: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: calc(100% + 15px);
        background-color: $colorWhite; } } }
  // end COL


  // TITLE
  &__title {
    text-transform: uppercase;
    text-align: left;
    @include rmin(320) {
      padding-bottom: 60px; }
    @include rmin(768) {
      padding-bottom: 110px; }

    span {
      color: $colorAlizarin; } }
  // end TITLE


  // BTN
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 450px;
    background-color: $colorAlizarin;
    color: $colorWhite;
    font-weight: bold;
    @include transition(background-color);
    @include rmin(320) {
      height: 80px;
      font-size: 18px; }
    @include rmin(576) {
      height: 100px;
      font-size: 22px; }
    @include rmin(768) {
      height: 120px;
      font-size: 26px; }

    &:hover {
      background-color: $colorBlack;
      @include transition(background-color); } }
  // end BTN


  // IMG
  &__img {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; }
 }  // end IMG
// ===============
