// SECTION
// ===============
.section {
  min-height: calc(100vh - 59px);
  @include r(767) {
    min-height: 0 !important;
    height: auto;
    padding-top: 30px;
    padding-bottom: 30px; }

  .container {
    height: 100%;
    min-height: 700px;
    @include r(767) {
      min-height: 0; } } }
