// TRANSPARENCY
// ===============
.transparency {
  @include rmin(320) {
    padding-top: 42px;
    padding-bottom: 42px; }
  @include rmin(576) {
    padding-top: 82px;
    padding-bottom: 82px; }
  @include rmin(768) {
    padding-top: 122px;
    padding-bottom: 122px; }
  @include rmin(992) {
    padding-top: 222px;
    padding-bottom: 222px; }


  // ROW
  &__row {
    display: flex;
    @include rmin(320) {
      flex-direction: column;
      padding-left: 0; }
    @include rmin(768) {
      flex-direction: row; }
    @include rmin(1200) {
      padding-left: 63px; }
    @include rmin(1366) {
      padding-left: 133px; }

    &-left {
      @include rmin(320) {
        width: 100%; }
      @include rmin(768) {
        width: 314px; } }

    &-right {
      padding-top: 11px;
      @include rmin(320) {
        width: 100%; }
      @include rmin(768) {
        width: calc(100% - 314px); } } }
  // end ROW


  // CIRCLE
  &__circle {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    background-color: $colorAlizarin;
    @include rmin(320) {
      width: 104px;
      height: 104px; }
    @include rmin(576) {
      width: 154px;
      height: 154px; }
    @include rmin(768) {
      width: 204px;
      height: 204px; }
    @include rmin(992) {
      width: 224px;
      height: 224px; }
    @include rmin(1200) {
      width: 254px;
      height: 254px; } }
  // end CIRCLE


  // TITLE
  &__title {
    padding-bottom: 22px;
    color: $colorAlizarin;
    text-transform: uppercase;
    font-weight: bold;
    @include rmin(320) {
      font-size: 14px; }
    @include rmin(576) {
      font-size: 16px; }
    @include rmin(728) {
      font-size: 18px; } }
  // end TITLE


  // TEXT
  &__text {
    width: 100%;
    max-width: 715px;
    line-height: 1.5;
    letter-spacing: 0.4px;
    font-weight: 500;
    font-family: "SFUIText", "Rajdhani", "Arial", sans-serif;
    @include rmin(320) {
      font-size: 16px; }
    @include rmin(576) {
      font-size: 22px; }
    @include rmin(992) {
      font-size: 32px; } }
 }  // end TEXT
// ===============
