// FOOTER
// ===============
.footer {
  background-color: $colorBlackRussian;

  //.about &,
  //.partnership &,
  //.infoCentre &
  //  +rmin(320)
  //    margin-top: 50px
  //  +rmin(768)
  //    margin-top: 100px

  // CONTAINER
  .container {
    padding-top: 60px;
 }    //border-top: 1px solid rgba($colorBlack, 0.1)
  // \ CONTAINER


  // ROW
  &__row {
    display: flex;
    @include rmin(320) {
      flex-direction: column;
      align-items: flex-start; }
    @include rmin(768) {
      flex-direction: row; }

    &-0 {
      align-items: center;
      padding-bottom: 50px;
      @include r(767) {
        padding-bottom: 0; } } }
  // \ ROW


  // COL
  &__col {
    width: 50%;
    &:nth-child(1) {
      @include rmin(768) {
        padding-right: 15px; }
      @include rmin(992) {
        padding-right: 35px; } }
    &:nth-child(2) {
      @include rmin(320) {
        padding-bottom: 50px; }
      @include rmin(768) {
        padding-left: 15px;
        padding-bottom: 0; }
      @include rmin(992) {
        padding-left: 35px; } }

    @include r(575) {
      width: 100%; }

    .footer__row-0 & {
      &:nth-child(1) {
        @include r(575) {
          display: none; } } }

    .footer__row-0 &,
    .footer__row-3 & {
      display: flex;

      &:nth-child(1) {
        justify-content: flex-start; }
      &:nth-child(2) {
        justify-content: flex-end;
        @include r(575) {
          justify-content: center; } } } }
 }  // \ COL
// ===============
