// MAIN
// ===============
.onlineTrading {
  .main {
    position: relative;
    @include rmin(320) {
      padding-top: 101px;
      padding-bottom: 30px; }
    @include rmin(768) {
      padding-top: 201px;
      padding-bottom: 60px; }


    // BG
    &__bg {
      z-index: 0;
      position: absolute;
      left: 0;
      width: 100%;

      &-0 {
        top: 0;
        height: calc(100% - 460px);
        background-color: $colorBlackRussian;
        background-image: url("../img/img-onlineTrading_bg@2x.png");
        background-repeat: no-repeat;
        background-position: right top;
        @include rmin(320) {
          background-size: cover; }
        @include rmin(992) {
          background-size: contain; } }

      &-1 {
        bottom: 0;
        height: 460px;
        background-color: $colorWhite; } }
    // end BG


    // CONTAINER
    .container {
      z-index: 1;
      position: relative;

      &--mobile {
        @include r(576) {
          padding-left: 0;
          padding-right: 0; } } }

    // end CONTAINER


    // TITLE
    &__title {
      color: $colorWhite;
      @include rmin(320) {
        padding-bottom: 30px;
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        padding-bottom: 84px;
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end TITLE


    // ROW
    &__row {
      display: flex;
      @include rmin(320) {
        flex-direction: column;
        padding-bottom: 52px; }
      @include rmin(992) {
        flex-direction: row;
        padding-bottom: 152px; } }
    // end ROW


    // COL
    &__col {
      &:nth-child(1) {
        @include rmin(320) {
          width: 100%; }
        @include rmin(992) {
          width: 437px; }
        @include rmin(1200) {
          width: 467px; }
        @include rmin(1366) {
          width: 497px; } }
      &:nth-child(2) {
        padding-top: 30px;
        @include rmin(320) {
          width: 100%; }
        @include rmin(992) {
          padding-left: 74px;
          width: calc(100% - 437px); }
        @include rmin(1200) {
          padding-left: 134px;
          width: calc(100% - 467px); }
        @include rmin(1366) {
          padding-left: 164px;
          width: calc(100% - 497px); } } }
    // end COL


    // INFO
    &__info {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      background-color: $colorAlizarin;
      color: $colorWhite;
      @include rmin(320) {
        padding: 15px; }
      @include rmin(768) {
        padding: 45px 25px; }
      @include rmin(992) {
        padding: 55px 30px; }
      @include rmin(1200) {
        padding: 65px 40px; }
      @include rmin(1366) {
        padding: 75px 50px; } }
    // end INFO


    // SUBTITLE
    &__subtitle {
      text-align: left; }
    // end SUBTITLE


    // TEXT
    &__text {
      width: 100%;
      color: $colorWhite;
      text-align: left;
      &:nth-child(1) {
        padding-bottom: 30px; }

      @include rmin(320) {
        max-width: 100%; }
      @include rmin(992) {
        max-width: 528px; } }
    // end TEXT


    // TABLE
    &__table {
      position: relative;
      @include rmin(320) {
        overflow-x: auto;
 }        //padding: 30px
      @include rmin(992) {
        overflow-x: initial; }
      @include rmin(1200) {
        overflow-x: initial;
        margin-left: 0; }
      @include rmin(1366) {
        overflow-x: initial;
        margin-left: 159px;
 }        //padding: 0

      &-wrap {
        @include rmin(320) {
          min-width: 872px; }
        @include rmin(992) {
          min-width: 0; } }

      &-bg {
        z-index: 0;
        position: absolute;
        //width: calc(100% / 5)
        //width: 354px
        box-shadow: 0 1px 30px 0 rgba($colorBlack, 0.1);
        @include rmin(320) {
          display: none; }
        @include rmin(992) {
          display: block;
          width: calc(100% / 3); }
        @include rmin(1200) {
          width: 354px; }

        &-0 {
          top: 108px;
          left: 0;
          height: calc(100% - (108px + 100px)); }

        &-1,
        &-3 {
          //top: 10px
          top: 0;
          //height: calc(100% - (10px + 10px))
          height: calc(100%); }

        &-2,
        &-4 {
          top: 0;
          height: 100%; }

        &-1 {
          left: 354px;
          @include rmin(992) {
            left: calc((100% / 3)); }
          @include rmin(1200) {
            left: 354px; } }

        &-2 {
          left: (354px * 2);
          @include rmin(992) {
            left: calc((100% / 3) * 2); }
          @include rmin(1200) {
            left: (354px * 2); } }

        //&-3
        //right: calc(100% / 5)

        //&-4
 }        //right: 0

      &-row {
        z-index: 1;
        position: relative;
        display: flex;
        width: 100%;
        font-family: "SFUIText", "Arial", sans-serif;

        &-0 {
          .main__table-col {

            font-weight: bold;
            font-family: "Rajdhani", Arial, sans-serif;
            @include rmin(320) {
              font-size: 18px; }
            @include rmin(1200) {
              font-size: 22px; } } }

        &-1,
        &-3,
        &-5,
        &-7,
        &-9 {
          .main__table-col {
            &:nth-child(odd) {
              background-color: $colorWhiteSmokeLight; }
            &:nth-child(even) {
              background-color: $colorSnow; } } }
        &-0,
        &-2,
        &-4,
        &-6,
        &-8,
        &-10, {
          .main__table-col {
            &:nth-child(odd) {
              background-color: $colorSnow; }
            &:nth-child(even) {
              background-color: $colorWhite; } } }

        &-0,
        &-10 {
          .main__table-col {
            &-0 {
              background-color: transparent !important; } } }

        &-0 {
          align-items: flex-end;

          .main__table-col {
            &-1,
            &-3 {
              //padding-top: 36px
              padding-top: 46px;
              padding-bottom: 36px; }

            &-2,
            &-4 {
              padding-top: 46px;
              padding-bottom: 36px; } } }

        &-10 {
          align-items: flex-start;

          .main__table-col {
            &-1,
            &-3 {
              padding-top: 30px;
              padding-bottom: 20px; }

            &-2,
            &-4 {
              padding-top: 30px;
              padding-bottom: 30px; } } } }


      &-col {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        //max-width: calc(100% / 5)

        padding: 20px 30px;
        @include rmin(320) {
          font-size: 14px; }
        @include rmin(992) {
          max-width: calc(100% / 3); }
        @include rmin(1200) {
          max-width: 354px;
          font-size: 18px; }

        &-0 {
          justify-content: flex-start; } }

      &-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        max-width: 145px;
        height: 40px;
        border-radius: 3px;
        border: 1px solid rgba($colorGrey, 0.1);
        background-color: transparent;
        font-weight: 600;
        @include transition(border-color, background-color, color);
        @include rmin(320) {
          font-size: 14px; }
        @include rmin(1200) {
          font-size: 16px; }

        &:hover {
          border-color: $colorAlizarin;
          background-color: $colorAlizarin;
          color: $colorWhite;
          @include transition(border-color, background-color, color); } } }
 } }    // end TABLE
// ===============
