// THOUGHTS
// ===============
.thoughts {
  border-bottom: 1px solid rgba($colorBlack, 0.1);
  @include rmin(320) {
    padding-top: 99px;
    padding-bottom: 100px; }
  @include rmin(768) {
    padding-top: 198px;
    padding-bottom: 200px; }


  // TITLE
  &__title {
    padding-bottom: 10px; }
  // \ TITLE


  // TEXT
  &__text {
    padding-bottom: 40px; }
  // \ TEXT


  // ROW
  &__row {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 751px;
    margin-left: auto;
    margin-right: auto;
    @include rmin(320) {
      flex-direction: column; }
    @include rmin(768) {
      flex-direction: row; } }
  // \ ROW


  // BLOCK
  &__block {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 190px;
    border: 1px solid rgba($colorBlack, 0.1);
    @include transition(box-shadow);
    @include rmin(320) {
      max-width: 100%; }
    @include rmin(768) {
      max-width: 48%; }
    @include rmin(992) {
      max-width: 356px; }

    &-0 {
      @include rmin(320) {
        margin-bottom: 30px; }
      @include rmin(768) {
        margin-bottom: 0; } }

    &:hover {
      box-shadow: 0 0 25px 3px rgba($colorBlack, 0.05);
      @include transition(box-shadow); }

    .icon {
      @include rmin(320) {
        margin-bottom: 15px; }
      @include rmin(768) {
        margin-bottom: 26px; } }

    span {
      font-weight: bold;
      @include rmin(320) {
        font-size: 18px; }
      @include rmin(576) {
        font-size: 20px; }
      @include rmin(768) {
        font-size: 22px; } } }
 }  // \ BLOCK
// ===============
