// SERVICE
// ===============
.service {
  padding-top: 111px;
  @include rmin(320) {
    padding-bottom: 95px; }
  @include rmin(768) {
    padding-bottom: 195px; }


  // COVER
  &__cover {
    @include rmin(768) {
      padding-left: 40px; }

    &-title {
      padding-bottom: 25px;
      font-size: 24px;
      font-weight: bold; }

    &-phone {
      display: inline-block;
      color: $colorAlizarin;
      @include r(767) {
        display: block;
        text-align: left; }

      &:last-child {
        @include rmin(320) {
          margin-left: 0; }
        @include rmin(768) {
          margin-left: 125px; } } } }
 }  // \ COVER
// ===============
