// SECTION-2
// ===============
.section-2 {
  @include rmin(320) {
    background-color: $colorWhiteSmoke; }
  @include rmin(768) {
    background-color: $colorWhite; }


  .section__title {
    text-align: center;
    font-weight: 700;
    @include rmin(320) {
      padding: 15px 0 15px;
      font-size: 32px; }
    @include rmin(768) {
      padding: 60px 0 40px; }
    @include rmin(1200) {
      font-size: 42px; }
    @include rmin(1440) {
      font-size: 60px; }
    @include r(767) {
      .home.is-loader & {
        animation-delay: 3.1s; } } }



  .section__row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    height: 80%; }


  .section__col {
    overflow: hidden;
    cursor: pointer;
    position: relative;
    width: calc(100% / 3);
    height: calc(100% / 3);
    min-height: 240px;
    padding: 15px;
    @include rmin(768) {
      &:hover {
        svg {
          opacity: 0.1;
          @include transition(opacity); }
        .text__wrap {
          transform: translateY(-100%);
          @include transition(transform);
          p {
            opacity: 1;
            @include transition(opacity); } } } }
    @include r(768) {
      max-height: 160px; }
    @include r(1024) {
      width: calc(100% / 2);
      height: calc(100% / 2); }

    &-0 {
      @include r(767) {
        .home.is-loader & {
          animation-delay: 3.2s; } } }
    &-1 {
      @include r(767) {
        .home.is-loader & {
          animation-delay: 3.3s; } } }


    svg {
      display: none;
      position: relative;
      @include transition(opacity);

      #ico2-line3,
      #ico2-line4,
      #ico2-line5,
      #svg3-use-0,
      #svg3-use-1 {
        @include rmin(320) {
          fill: $colorWhiteSmoke; }
        @include rmin(768) {
          fill: $colorWhite; } } }

    .svg__wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 160px;
      margin-bottom: 30px; }


    .text__wrap {
      position: relative;
      padding: 0 30px;
      text-align: center;
      transform: translateY(0);
      @include transition(transform);
      @include r(768) {
        padding: 0; }

      h3 {
        margin-bottom: 15px;
        font-size: 22px;
        @include r(768) {
          font-size: 16px; } }
      p {
        opacity: 0;
        font-size: 18px;
        width: 75%;
        margin-left: auto;
        margin-right: auto;
        @include transition(opacity); } } } }


svg {
  .transparent {
    @include rmin(320) {
      fill: transparent; }
    @include rmin(768) {
      fill: #fff; } } }

.animation-go-js {
  svg {
    display: block !important;

    path,
    rect,
    ellipse,
    polyline {
      stroke-dasharray: 1000;
      stroke-dashoffset: 1000;
      animation: dash 2.5s ease-in-out forwards;
      animation-delay: 0.85s; }

    .animated01 {
      opacity: 0;
      animation-name: fadeInCustom01;
      animation-duration: 2s;
      animation-delay: 1s; }
    .animated1 {
      opacity: 0;
      animation-name: fadeInCustom1;
      animation-duration: 2s;
      animation-delay: 2s; } }

  @keyframes dash {
    from {
      stroke-dashoffset: 1000; }
    to {
      stroke-dashoffset: 0; } }

  @keyframes fadeInCustom01 {
    from {
      opacity: 0; }
    to {
      opacity: 0.1; } }

  @keyframes fadeInCustom1 {
    from {
      opacity: 0; }
    to {
      opacity: 1; } } }
