// ADDRESS
// ===============
.address {
  @include rmin(320) {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include rmin(768) {
    padding-top: 130px;
    padding-bottom: 150px; }

  // ROW
  &__row {
    display: flex;
    @include rmin(320) {
      flex-direction: column; }
    @include rmin(768) {
      flex-direction: row; } }
  // \ ROW


  // COL
  &__col {
    @include rmin(320) {
      width: 100%; }
    @include rmin(768) {
      width: 50%; }

    &:last-child {
      @include r(767) {
        margin-top: 30px; } }

    &-title {
      padding-bottom: 18px;
      text-transform: uppercase;
      font-weight: bold;
      @include rmin(320) {
        font-size: 16px; }
      @include rmin(576) {
        font-size: 20px; }
      @include rmin(728) {
        font-size: 24px; } }

    &-text {
      padding-bottom: 20px;
      font-family: "SFUIText", "Arial", sans-serif;
      @include rmin(320) {
        font-size: 14px; }
      @include rmin(576) {
        font-size: 16px; }
      @include rmin(728) {
        font-size: 18px; }

      &:last-child {
        padding-bottom: 0; } } }
 }  // \ COL
// ===============
