// MODAL
// ===============
.modal {
  position: relative;
  width: 100%;
  min-height: 250px;
  margin: 40px auto;
  background-color: $colorWhite;
  box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 0.15); }


#modal {
  &.modal {
    max-width: 290px;
    min-height: 250px;
    border-radius: 15px;

    &__body {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100%;
      padding: 30px;

      svg {
        margin-bottom: 10px; }

      h3 {
        margin-bottom: 15px;
        font-size: 18px; }

      p {
        font-size: 14px;
        line-height: 1.6;
        font-family: "SF Pro Text", "Rajdhani", "Arial", sans-serif;
        text-align: center; } } }

  .mfp-close {
    opacity: 1;
    top: auto;
    bottom: -55px;
    left: 0;
    right: 0;
    width: 40px;
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 100%;
    background-color: $colorWhite;
    @include transition(opacity, box-shadow, transform);

    &:hover {
      opacity: 0.9;
      box-shadow: 0 0 5px 0 rgba($colorBlack, 0.45);
      transform: rotate(180deg);
      @include transition(opacity, box-shadow, transform); } } }


#modalthoughts {
  &.modal {
    max-width: 526px;
    @include rmin(320) {
      padding: 30px 35px; }
    @include rmin(768) {
      padding: 60px 65px; }

    .modal__title {
      padding-bottom: 30px;
      text-align: left;
      font-weight: bold; }

    .mfp-close {
      font-size: 0;
      @include sprite(close);
      @include rmin(320) {
        top: 10px;
        right: 10px; }
      @include rmin(768) {
        top: 30px;
        right: 30px; } } } }

.mfp-ready #modalDirectors .modal__body {
  animation-name: slideInRight, fadeIn;
  animation-delay: 0.05s; }

#modalDirectors {
  &.modal {
    display: flex;
    align-items: center;
    top: 0;
    right: 0;
    left: auto;
    min-height: 100vh;
    margin: 0;
    margin-left: auto;
    background-color: $colorWhite;
    @include rmin(320) {
      width: 95%;
      padding: 40px; }
    @include rmin(768) {
      width: 720px;
      padding: 60px; }
    @include rmin(992) {
      padding: 80px; }

    .mfp-close {
      font-size: 0;
      @include sprite(close);
      @include rmin(320) {
        top: 10px;
        right: 10px; }
      @include rmin(768) {
        top: 20px;
        right: 20px; }
      @include rmin(992) {
        top: 30px;
        right: 30px; } } }

  .modal__body {
    &-top {
      display: flex;
      align-items: center;
      padding-bottom: 60px; }

    &-left {
      margin-right: 30px; }

    &-right {
      p {
        &:nth-child(1) {
          padding-bottom: 10px;
          font-weight: bold;
          font-family: "SFUIText", "Arial", sans-serif;
          @include rmin(320) {
            font-size: 18px; }
          @include rmin(576) {
            font-size: 22px; }
          @include rmin(728) {
            font-size: 26px; } }

        &:nth-child(2) {
          text-transform: uppercase;
          font-family: "SFUIText", "Arial", sans-serif;
          font-size: 12px;
          color: rgba($colorBlack, 0.7); } } }


    &-bottom {
      p {
        line-height: 1.6;
        font-family: "SFUIText", "Arial", sans-serif;
        @include rmin(320) {
          font-size: 14px; }
        @include rmin(576) {
          font-size: 16px; }
        @include rmin(728) {
          font-size: 18px; } } } } }


.show.mfp-bg {
  background-color: rgba($colorBlack, 0.5);
  animation-duration: 0.35s;
  animation-fill-mode: both;
  animation-name: fadeIn; }


.mfp-close-btn-in .mfp-close {
  color: $colorBlack; }

.about .mfp-container {
  padding: 0; }

// ===============

.btn-close {
  color: #aaaaaa;
  font-size: 43px;
  text-decoration: none;
  padding: 10px;
  position: absolute;
  right: 7px;
  top: -13px; }

.btn-close:hover {
  color: #919191; }

.modale:before {
  content: "";
  display: none;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10; }

.opened:before {
  display: block; }

.opened .modal-dialog {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
  top: 15%; }

.modal-dialog {
  background: #fefefe;
  border: #333333 solid 0px;
  border-radius: 5px;
  margin-left: -400px;
  text-align: center;
  position: fixed;
  left: 50%;
  top: -100%;
  z-index: 11;
  width: 800px;
  box-shadow: 0 5px 10px rgba(0,0,0,0.3);
  transform: translate(0, -500%);
  transition: transform 0.3s ease-out; }

.modal-body {
  padding: 20px; }


.modal-body iframe {
  height: 520px; }


.modal-header,
.modal-footer {
  padding: 10px 20px; }


@media only screen and (max-width: 860px) {
  .modal-dialog {
    width: 550px;
    margin-left: -270px; } }


@media only screen and (max-width: 550px) {
  .modal-dialog {
    width: 300px;
    margin-left: -150px; } }


.modal-body iframe {
  height: 460px; }


