// MAIN
// ===============
.economicCalendar {
  .main {
    position: relative;
    @include rmin(320) {
      padding-top: 101px; }
    @include rmin(768) {
      padding-top: 201px; }


    // TITLE
    &__title {
      padding-bottom: 50px;
      line-height: normal;
      @include rmin(320) {
        font-size: 32px; }
      @include rmin(576) {
        font-size: 36px; }
      @include rmin(768) {
        font-size: 48px; }
      @include rmin(992) {
        font-size: 62px; }
      @include rmin(1200) {
        font-size: 75px; }
      @include rmin(1440) {
        font-size: 90px; }
      @include rmin(1920) {
        font-size: 120px; } }
    // end TITLE


    // ROW
    &__row {
      overflow-y: auto;
      width: 100%;
      height: 800px;
      margin-bottom: 50px;
      border-top: 1px solid rgba($colorBlack, 0.1); }
 } }    // \ ROW
// ===============
