// AUTOCHARTIST
// ===============
.autochartist {
  @include rmin(320) {
    padding-top: 45px;
    padding-bottom: 50px; }
  @include rmin(768) {
    padding-top: 90px;
    padding-bottom: 101px; }


  // TITLE
  &__title {
    @include rmin(320) {
      padding-bottom: 45px; }
    @include rmin(768) {
      padding-bottom: 90px; } }
  // \ TITLE


  // ROW
  &__row {
    display: flex;
    flex-wrap: wrap; }
  // \ROW


  // BLOCK
  &__block {
    display: flex;
    @include rmin(320) {
      width: 100%;
      margin-bottom: 50px; }
    @include rmin(768) {
      width: 50%;
      padding-right: 40px; }
    @include rmin(992) {
      margin-bottom: 60px;
      padding-right: 70px; }
    @include rmin(1200) {
      padding-right: 100px; }

    &-left {
      @include rmin(320) {
        width: 80px; }
      @include rmin(768) {
        width: 100px; } }

    &-right {
      font-family: "SFUIText", "Arial", sans-serif;
      @include rmin(320) {
        width: calc(100% - 80px); }
      @include rmin(768) {
        width: calc(100% - 100px); } }

    &-title,
    &-text {
      font-size: 18px;
      @include rmin(320) {
        font-size: 14px; }
      @include rmin(576) {
        font-size: 16px; }
      @include rmin(728) {
        font-size: 18px; } }

    &-title {
      padding-bottom: 10px;
      font-weight: bold; }

    &-text {
      line-height: 1.5; } } }
// \ BLOCK
// ===============
